<template>
  <div>
    <md-whiteframe class="nippo-list" md-elevation="1">
      <md-list v-if="Object.keys(this.$store.state.reports).length > 0 "class="nippo-form" md-elevation="1">
        <template v-for="report in reports">
          <md-list-item @click="">
            <router-link :to="{ name: 'report', params: { id: report.id }}">
              <span class="nippo-title">{{report.title}}</span><span class="nippo-date">({{report.date}})</span>
            </router-link>
          </md-list-item>
          <md-divider></md-divider>
        </template>
      </md-list>
      <template v-else>
        GO WRITE SOME NIPPOS NOW!!
      </template>
    </md-whiteframe>
    <md-button @click.native="onClickAdd" class="md-fab md-fab-bottom-right">
      <md-icon>add</md-icon>
    </md-button>
  </div>
</template>

<script type="text/javascript">
require('vue-material/dist/vue-material.css')
export default {
  name: 'list',
  computed: {
    reports () {
      return Object.values(this.$store.state.reports)
    }
  },
  methods: {
    onClickAdd () {
      this.$router.push('/form')
    },
    onClickReportItem () {
      this.$store.dispatch('startEdit')
    }
  }
}
</script>

<style type="text/css">
  .nippo-list {
    margin: auto;
    max-width: 80vw;
    padding: 16px;
  }
  @media only screen
  and (min-device-width: 320px)
  and (max-device-width: 568px)
  and (-webkit-min-device-pixel-ratio: 2) {
    .nippo-list {
      max-width: none;
    }
  }
</style>



